`, "",{"version":3,"sources":["webpack://./src/ui/views/NewAccount.vue"],"names":[],"mappings":";AAonBA;IACA,gBAAA;IACA,cAAA;AACA","sourcesContent":["<template>\n  <v-container>\n    <v-card\n      class=\"options mt-3\">\n      <v-stepper v-model=\"currentStep\">\n        <v-stepper-header>\n          <v-stepper-step\n            :complete=\"currentStep > 0\"\n            step=\"0\" />\n          <v-divider />\n          <v-stepper-step\n            :complete=\"currentStep > 1\"\n            step=\"1\" />\n          <v-divider />\n          <v-stepper-step\n            :complete=\"currentStep > 2\"\n            step=\"2\" />\n          <v-divider />\n          <v-stepper-step\n            step=\"3\"\n            :complete=\"currentStep > 3\" />\n          <v-divider />\n          <v-stepper-step\n            step=\"4\"\n            :complete=\"currentStep > 4\" />\n        </v-stepper-header>\n\n        <v-stepper-items>\n          <v-stepper-content step=\"0\">\n            <div class=\"headline\">\n              {{ t('LabelChooseadapter') }}\n            </div>\n            <v-form>\n              <v-radio-group v-model=\"adapter\">\n                <div\n                  v-for=\"a in adapters\"\n                  :key=\"a.type\">\n                  <v-radio\n                    :value=\"a.type\">\n                    <template #label>\n                      <div class=\"heading\">\n                        {{ a.label }}\n                      </div>\n                    </template>\n                  </v-radio>\n                  <div class=\"caption pl-8 mb-5\">\n                    {{ a.description }}\n                  </div>\n                </div>\n              </v-radio-group>\n            </v-form>\n            <div class=\"d-flex flex-row-reverse\">\n              <v-btn\n                class=\"primary\"\n                @click=\"currentStep++\">\n                {{ t('LabelContinue') }}\n              </v-btn>\n              <v-btn\n                :to=\"{ name: 'IMPORTEXPORT' }\"\n                class=\"mr-2\">\n                <v-icon>mdi-export</v-icon>\n                <template v-if=\"isBrowser && true\">\n                  {{ t('LabelImportExport') }}\n                </template>\n              </v-btn>\n            </div>\n          </v-stepper-content>\n\n          <v-stepper-content step=\"1\">\n            <div class=\"headline\">\n              {{ t('LabelAccountlabel') }}\n            </div>\n            <v-form>\n              <v-text-field\n                v-model=\"label\"\n                append-icon=\"mdi-label\"\n                class=\"mt-2 mb-4\"\n                :label=\"t('LabelAccountlabel')\"\n                :hint=\"t('DescriptionAccountlabel')\"\n                :persistent-hint=\"true\"\n                @keydown.enter.prevent=\"currentStep++\" />\n            </v-form>\n            <div class=\"d-flex flex-row justify-space-between\">\n              <v-btn @click=\"currentStep--\">\n                {{ t('LabelBack') }}\n              </v-btn>\n              <v-btn\n                class=\"primary\"\n                @click=\"currentStep++\">\n                {{ t('LabelContinue') }}\n              </v-btn>\n            </div>\n          </v-stepper-content>\n\n          <v-stepper-content step=\"2\">\n            <template v-if=\"adapter === 'nextcloud-bookmarks'\">\n              <div class=\"headline\">\n                {{ t('LabelServersetup') }}\n              </div>\n              <v-text-field\n                v-model=\"server\"\n                :rules=\"[validateUrl]\"\n                :label=\"t('LabelNextcloudurl')\"\n                :loading=\"isServerTestRunning || isLoginFlowRunning\"\n                :error-messages=\"serverisNotHttps || serverTestError || loginFlowError\"\n                @keydown.enter=\"testNextcloudServer\">\n                <template\n                  slot=\"append-outer\">\n                  <v-icon\n                    v-if=\"serverTestSuccessful\"